// src/App.js
import React, { useState, useEffect } from 'react';
import { ethers } from 'ethers';
import { connectWallet, getLoans } from './ethereum';
import Header from './components/Header';
import LoanList from './components/LoanList';

function App() {
  const [account, setAccount] = useState('');
  const [isConnected, setIsConnected] = useState(false);
  const [amount, setAmount] = useState('');
  const [interestRate, setInterestRate] = useState('');
  const [duration, setDuration] = useState('');
  const [transactionData, setTransactionData] = useState(null);
  const [contract, setContract] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [loans, setLoans] = useState([]);
  const [activeTab, setActiveTab] = useState('create'); // 'create' or 'list'
  const [isLoadingLoans, setIsLoadingLoans] = useState(false);
  const [lastAction, setLastAction] = useState(''); // 'create', 'fund', or 'repay'

  // Check if already connected on page load
  useEffect(() => {
    const checkConnection = async () => {
      if (window.ethereum) {
        try {
          const accounts = await window.ethereum.request({ method: 'eth_accounts' });
          if (accounts.length > 0) {
            setAccount(accounts[0]);
            setIsConnected(true);
            
            // Get contract instance
            const connection = await connectWallet();
            if (connection.contract) {
              setContract(connection.contract);
              loadLoans(connection.contract);
            }
          }
        } catch (error) {
          console.error("Error checking connection:", error);
        }
      }
    };
    
    checkConnection();
    
    // Listen for account changes
    if (window.ethereum) {
      window.ethereum.on('accountsChanged', (accounts) => {
        if (accounts.length > 0) {
          setAccount(accounts[0]);
          setIsConnected(true);
        } else {
          setAccount('');
          setIsConnected(false);
        }
      });
    }
  }, []);

  const loadLoans = async (contractInstance) => {
    try {
      setIsLoadingLoans(true);
      const contractToUse = contractInstance || contract;
      
      if (!contractToUse) {
        console.error("No contract instance available to load loans");
        return;
      }
      
      console.log("Getting loans from contract:", await contractToUse.getAddress());
      const loansList = await getLoans(contractToUse);
      console.log("Loans loaded:", loansList);
      
      // Separate loans by status
      const pendingLoans = loansList.filter(loan => 
        !loan.repaid && loan.lender === '0x0000000000000000000000000000000000000000'
      );
      
      const activeLoans = loansList.filter(loan => 
        !loan.repaid && loan.lender !== '0x0000000000000000000000000000000000000000'
      );
      
      const repaidLoans = loansList.filter(loan => loan.repaid);
      
      // Sort by ID (most recent first)
      const sortById = (a, b) => parseInt(b.id) - parseInt(a.id);
      
      // Combine: pending first, then active, then repaid
      const sortedLoans = [
        ...pendingLoans.sort(sortById),
        ...activeLoans.sort(sortById),
        ...repaidLoans.sort(sortById)
      ];
      
      setLoans(sortedLoans);
    } catch (error) {
      console.error("Error loading loans:", error);
    } finally {
      setIsLoadingLoans(false);
    }
  };

  const handleConnectWallet = async () => {
    try {
      console.log("Connecting wallet from App component...");
      const connection = await connectWallet();
      console.log("Connection result:", connection);
      
      if (connection.address) {
        setAccount(connection.address);
        setIsConnected(true);
        setContract(connection.contract);
        console.log("Wallet connected successfully:", connection.address);
        
        // Load loans after connecting
        loadLoans(connection.contract);
      } else if (connection.error) {
        console.error("Connection returned error:", connection.error);
      }
    } catch (error) {
      console.error("Error in handleConnectWallet:", error);
      alert(`Failed to connect wallet: ${error.message || "Unknown error"}`);
    }
  };

  const formatAddress = (address) => {
    if (!address) return '';
    return `${address.substring(0, 6)}...${address.substring(address.length - 4)}`;
  };

  const createLoan = async (e) => {
    e.preventDefault();
    console.log('Creating loan:', { amount, interestRate, duration });
    
    try {
      setIsLoading(true);
      setLastAction('create');
      
      // Use the actual contract instance
      const contractAddress = await contract.getAddress();
      
      // Convert values to appropriate formats for the smart contract
      const amountInWei = ethers.parseEther(amount.toString());
      const interestRateValue = parseInt(interestRate * 100); // Convert to basis points (e.g., 5% = 500)
      const durationInDays = parseInt(duration);
      
      // Call the smart contract createLoan function
      // Assuming standard ERC20 token - replace with appropriate token address
      const daiTokenAddress = "0x6B175474E89094C44Da98b954EedeAC495271d0F"; // Example: DAI token on mainnet
      
      // Call the createLoan function on your smart contract
      const tx = await contract.createLoan(
        daiTokenAddress, 
        amountInWei, 
        interestRateValue, 
        durationInDays, 
        false // Not collateralized for this example
      );
      
      // Wait for the transaction to be mined
      const receipt = await tx.wait();
      
      // Get transaction data
      const txData = {
        hash: receipt.hash,
        blockNumber: receipt.blockNumber,
        from: receipt.from,
        to: receipt.to,
        gasUsed: receipt.gasUsed.toString()
      };
      
      setTransactionData(txData);
      
      // Reset form fields
      setAmount('');
      setInterestRate('');
      setDuration('');
      
      // Reload loans after creating a new one
      await loadLoans();
      
      // Switch to the loan list tab
      setActiveTab('list');
      
    } catch (error) {
      console.error("Error creating loan:", error);
      alert("Error creating loan. Check console for details.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleTransactionComplete = async (result, type) => {
    setTransactionData(result);
    setLastAction(type);
    
    // Reload loans to get the updated state
    await loadLoans();
  };

  return (
    <div className="app-container">
      <header className="app-header">
        <h1>DeFi Lending Platform</h1>
        <button onClick={handleConnectWallet} className="connect-button">
          {isConnected 
            ? `Connected: ${formatAddress(account)}` 
            : 'Connect Wallet'}
        </button>
      </header>
      
      {isConnected ? (
        <>
          <div className="tabs">
            <button 
              className={`tab-button ${activeTab === 'create' ? 'active' : ''}`}
              onClick={() => setActiveTab('create')}
            >
              Create Loan
            </button>
            <button 
              className={`tab-button ${activeTab === 'list' ? 'active' : ''}`}
              onClick={() => { setActiveTab('list'); loadLoans(); }}
            >
              View Loans
            </button>
          </div>
          
          {activeTab === 'create' ? (
            <>
              <section className="user-guide">
                <h2>Create a New Loan Request</h2>
                <ol>
                  <li>Enter the loan details including amount, interest rate, and duration.</li>
                  <li>Click "Create Loan" to submit your loan request to the blockchain.</li>
                  <li>Switch to the "View Loans" tab to see your loan and its status.</li>
                </ol>
              </section>
              <form onSubmit={createLoan} className="loan-form">
                <h2>Create a Loan</h2>
                <input
                  type="number"
                  placeholder="Amount (ETH)"
                  value={amount}
                  onChange={(e) => setAmount(e.target.value)}
                  required
                />
                <input
                  type="number"
                  placeholder="Interest Rate (%)"
                  value={interestRate}
                  onChange={(e) => setInterestRate(e.target.value)}
                  required
                />
                <input
                  type="number"
                  placeholder="Duration (days)"
                  value={duration}
                  onChange={(e) => setDuration(e.target.value)}
                  required
                />
                <button 
                  type="submit" 
                  className="submit-button"
                  disabled={isLoading}
                >
                  {isLoading ? "Processing..." : "Create Loan"}
                </button>
              </form>
            </>
          ) : (
            <section className="loans-section">
              <div className="loan-instructions">
                <h2>Loan Marketplace</h2>
                <p>Browse available loans, fund requests from other users, or repay your active loans.</p>
                <ul>
                  <li><strong>Fund</strong>: Lend money to borrowers and earn interest.</li>
                  <li><strong>Repay</strong>: Pay back loans you've borrowed, including interest.</li>
                  <li><strong>Create</strong>: Switch to the "Create Loan" tab to request funds.</li>
                </ul>
              </div>
              
              {transactionData && (
                <section className="transaction-details">
                  <h2>
                    {lastAction === 'create' ? 'Loan Created' : 
                     lastAction === 'fund' ? 'Loan Funded' : 
                     'Loan Repaid'} Successfully!
                  </h2>
                  <div className="transaction-grid">
                    <div className="transaction-item">
                      <span className="label">Transaction Hash:</span>
                      <span className="value">{transactionData.hash}</span>
                    </div>
                    <div className="transaction-item">
                      <span className="label">Block Number:</span>
                      <span className="value">{transactionData.blockNumber}</span>
                    </div>
                    <div className="transaction-item">
                      <span className="label">From:</span>
                      <span className="value">{transactionData.from}</span>
                    </div>
                    <div className="transaction-item">
                      <span className="label">To:</span>
                      <span className="value">{transactionData.to}</span>
                    </div>
                    <div className="transaction-item">
                      <span className="label">Gas Used:</span>
                      <span className="value">{transactionData.gasUsed}</span>
                    </div>
                  </div>
                  <div className="explorer-link">
                    <a 
                      href={`https://sepolia.etherscan.io/tx/${transactionData.hash}`} 
                      target="_blank" 
                      rel="noopener noreferrer"
                    >
                      View on Etherscan
                    </a>
                    <button 
                      className="clear-tx-button" 
                      onClick={() => setTransactionData(null)}
                    >
                      Dismiss
                    </button>
                  </div>
                </section>
              )}
              
              {isLoadingLoans ? (
                <div className="loading">Loading loans...</div>
              ) : (
                <LoanList 
                  loans={loans} 
                  contract={contract} 
                  address={account}
                  onTransactionComplete={handleTransactionComplete}
                />
              )}
            </section>
          )}
        </>
      ) : (
        <div className="welcome-message">
          <h2>Welcome to DeFi Lending Platform</h2>
          <p>Please connect your wallet to use the application.</p>
        </div>
      )}
    </div>
  );
}

export default App;